import type { InstantID } from '@/models/InstantID'
import { isPlainObject, invariant } from '@pspdfkit/shared'
import { Comment } from '@/models'
import {
  getCollaborationPermissionsFromJSON,
  ICollaboratorPermissionsOptions,
  serialiseCollaborationPermissionsProperties,
  validateCollaborationPermissionJSON,
} from './collaboration-permissions'
import type { CommentJSON } from '@/typings/json/comment'

export function toJSONComment(comment: Comment): CommentJSON {
  return {
    id: comment.id,
    type: 'pspdfkit/comment',
    v: 2,
    rootId: comment.rootId,
    pageIndex: comment.pageIndex,
    pdfObjectId: comment.pdfObjectId,
    creatorName: comment.creatorName,
    createdAt: comment.createdAt.toISOString(),
    updatedAt: comment.updatedAt.toISOString(),
    text: comment.text,
    customData: comment.customData,
    isAnonymous: comment.isAnonymous,
    ...serialiseCollaborationPermissionsProperties(comment),
  }
}

export function fromJSONComment(
  id: InstantID,
  json: Omit<CommentJSON, 'id' | 'group' | 'permissions' | 'isAnonymous'>,
  options?: ICollaboratorPermissionsOptions & {
    isAnonymous?: boolean | null
  },
): Comment {
  invariant(json.v === 2, 'Unknown comment version')
  invariant(json.type === 'pspdfkit/comment', 'Invalid comment type')
  invariant(typeof json.rootId === 'string', '`rootId` must be of type `string`')
  invariant(typeof json.pageIndex === 'number', '`pageIndex` must be of type `number`')
  invariant(
    json.pdfObjectId == null || typeof json.pdfObjectId === 'number',
    '`pdfObjectId` must be of type `number`',
  )
  invariant(
    json.creatorName == null || typeof json.creatorName === 'string',
    '`creatorName` must be of type `string`',
  )
  invariant(typeof json.text === 'object', '`text` must be of type `object`')
  invariant(typeof json.text.value === 'string', '`text.value` must be of type `string`')
  invariant(
    json.text.format === 'plain' || json.text.format === 'xhtml',
    '`text.format` must be `plain` or `xhtml`',
  )
  invariant(
    json.customData == null || isPlainObject(json.customData),
    '`customData` must be a JSON-serializable object',
  )
  invariant(
    typeof options?.isAnonymous === 'undefined' ||
      typeof options?.isAnonymous === 'boolean' ||
      options?.isAnonymous === null,
    '`isAnonymous` should be `undefined` or `boolean`.',
  )
  validateCollaborationPermissionJSON(options)

  return new Comment({
    id,
    rootId: json.rootId,
    pageIndex: json.pageIndex,
    pdfObjectId: json.pdfObjectId,
    creatorName: json.creatorName,
    createdAt: json.createdAt ? new Date(json.createdAt) : new Date(0),
    updatedAt: json.updatedAt ? new Date(json.updatedAt) : new Date(0),
    text: json.text,
    customData: json.customData,
    isAnonymous: options?.isAnonymous,
    ...getCollaborationPermissionsFromJSON(options),
  })
}
